import React, { useEffect, useState } from 'react';
import { SlList, SlGrid } from 'react-icons/sl';
import { useSelector, useDispatch } from 'react-redux';
import { removeProduct /*setProducts*/ } from '../redux/products';
import { TiArrowSortedDown, TiArrowSortedUp } from 'react-icons/ti';
import { BsTrash3 } from 'react-icons/bs';

import '../styles/productsList.scss';

function ProductsList() {
    const dispatch = useDispatch();
    const { nightMode } = useSelector((state) => state.nightMode);
    const { products } = useSelector((state) => state.products);
    const [expirationSorted, setCathegorySorted] = useState(true);
    const [listView, setListView] = useState(true);

    console.log(products);

    /*useEffect(() => {
        const fetchProducts = async () => {
            const response = await fetch('http://localhost:5000');
            const json = await response.json();
            dispatch(setProducts(json));
            //console.log(json);
        };
        fetchProducts();
    }, []);*/

    function countExpirationDays(expirationDate) {
        const expired = <span className='expired'>Expired !</span>;
        const expiringToday = <span className='expired'>Today !</span>;
        const todaysDate = new Date();
        let productDate = new Date(expirationDate);
        let difference = productDate.getTime() - todaysDate.getTime();
        let days = Math.ceil(difference / (1000 * 3600 * 24));

        if (days < 0) {
            return expired;
        } else if (days === 0) {
            return expiringToday;
        }
        return days <= 1 ? `${days} day` : `${days} days`;
    }

    const monthNames = [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
    ];

    function showProductDate(expirationDate) {
        let date = new Date(expirationDate);
        let day = date.getDate();
        let month = monthNames[date.getMonth()];
        let year = date.getFullYear();
        if (day < 10) {
            day = `0${day}`;
        }
        switch (month) {
            case 1:
                month = 'Jan';
                break;
            case 2:
                month = 'Feb';
                break;
            case 3:
                month = 'Mar';
                break;
            case 4:
                month = 'Apr';
                break;
            case 5:
                month = 'May';
                break;
            case 6:
                month = 'Jun';
                break;
            case 7:
                month = 'Jul';
                break;
            case 8:
                month = 'Aug';
                break;
            case 9:
                month = 'Sep';
                break;
            case 10:
                month = 'Oct';
                break;
            case 11:
                month = 'Nov';
                break;
            case 12:
                month = 'Dec';
                break;
            default:
                break;
        }
        let dateFormat = `${day} ${month} ${year}`;
        return dateFormat;
    }

    const handleDeleteProduct = /*async*/ (/*_id,*/ id) => {
        /* const response = await fetch('http://localhost:5000/' + _id, {
            method: 'DELETE',
        });
        const json = await response.json();
        console.warn(json);

        console.info(id + ' z mongoDB: ' + _id);
        if (response.ok) {
            dispatch(removeProduct(json));
        }*/
        dispatch(removeProduct(id));
    };

    const [sortedProducts, setSortedProducts] = useState([]);
    // Sortowanie produktów, gdy expirationSorted się zmienia
    useEffect(() => {
        let sorted = [...products];
        if (expirationSorted === true) {
            sorted.sort((a, b) => {
                let dateA = new Date(a.expirationDate);
                let dateB = new Date(b.expirationDate);
                return dateA - dateB;
            });
        } else if (expirationSorted === false) {
            sorted.sort((a, b) => {
                let dateA = new Date(a.expirationDate);
                let dateB = new Date(b.expirationDate);
                return dateB - dateA;
            });
        }

        setSortedProducts(sorted);
    }, [expirationSorted, products]);

    return (
        <>
            <div className={`items__list ${!listView && 'carts__view'}`}>
                <div className='select-view-options'>
                    <SlList
                        className='select-option'
                        onClick={() => setListView(true)}
                    />
                    <SlGrid
                        className='select-option'
                        onClick={() => setListView(false)}
                    />
                </div>

                <ul
                    className='categories__list__row'
                    style={{ display: `${listView ? 'flex' : 'none'}` }}
                ></ul>
            </div>

            <div className={listView ? '' : 'products__section'}>
                {[].map(() => (
                    <ul
                        className={`${
                            listView ? 'items__list__row' : 'items__grid'
                        } ${nightMode === true ? 'night__mode' : ''}`}
                        id='single__item'
                    >
                        <li
                            className={
                                listView
                                    ? 'items__list__row__position'
                                    : 'item__category'
                            }
                        >
                            category
                        </li>
                    </ul>
                ))}
            </div>

            <section className='products'>
                <div className='products__list-container'>
                    <ul className='products__nav'>
                        <li className='products_category'>Name</li>
                        <li className='products_category'>Category</li>
                        <li
                            className='products_category expiration-date'
                            onClick={() =>
                                setCathegorySorted(!expirationSorted)
                            }
                        >
                            Expiration Date
                            {expirationSorted ? (
                                <TiArrowSortedDown className='arrow-icon' />
                            ) : (
                                <TiArrowSortedUp className='arrow-icon' />
                            )}
                        </li>
                        <li className='products_category'>Remove</li>
                    </ul>
                    {/* MAP FUNCTION */}
                    {sortedProducts.map(
                        ({
                            productName,
                            cathegory,
                            expirationDate,
                            id,
                            _id,
                        }) => (
                            <ul className='product__row'>
                                <li className='proudctName-row'>
                                    {productName}
                                </li>
                                <li>
                                    <div className='category-row'>
                                        {cathegory}
                                        <img
                                            src={
                                                process.env.PUBLIC_URL +
                                                `/${cathegory}.png`
                                            }
                                            alt='cathegory thumbnail'
                                            className='cathegory-thumbnail'
                                        />
                                    </div>
                                </li>
                                <li className='expirationDate-row'>
                                    {showProductDate(expirationDate)}
                                    <p className='days__until '>
                                        expiry date:{' '}
                                        <br className='mobile-br' />
                                        <span>
                                            {countExpirationDays(
                                                expirationDate
                                            )}
                                        </span>
                                    </p>
                                </li>
                                <li>
                                    <button
                                        class='button'
                                        onClick={() =>
                                            handleDeleteProduct(id, _id)
                                        }
                                    >
                                        <svg
                                            viewBox='0 0 448 512'
                                            class='svgIcon'
                                        >
                                            <path d='M135.2 17.7L128 32H32C14.3 32 0 46.3 0 64S14.3 96 32 96H416c17.7 0 32-14.3 32-32s-14.3-32-32-32H320l-7.2-14.3C307.4 6.8 296.3 0 284.2 0H163.8c-12.1 0-23.2 6.8-28.6 17.7zM416 128H32L53.2 467c1.6 25.3 22.6 45 47.9 45H346.9c25.3 0 46.3-19.7 47.9-45L416 128z'></path>
                                        </svg>
                                    </button>
                                </li>
                            </ul>
                        )
                    )}
                </div>
            </section>
        </>
    );
}

export default ProductsList;
